.investment-page {
    width: 100%;
}

.investment-tabs {
    display: flex;
    gap: 8px;
    padding: 8px;
    margin-bottom: var(--spacing-xl);
    width: fit-content;
}

.tab-item {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 20px;
    border-radius: var(--border-radius-md);
    font-size: 0.85rem;
    font-weight: 700;
    color: var(--secondary);
    transition: all 0.3s ease;
    background: transparent;
    border: none;
    cursor: pointer;
    text-decoration: none;
}

.tab-item:hover {
    background: rgba(255, 255, 255, 0.05);
    color: #fff;
}

.tab-item.active {
    background: var(--accent);
    color: #fff;
    box-shadow: 0 4px 15px rgba(41, 151, 255, 0.3);
}

.account-summary-top {
    display: flex;
    gap: var(--spacing-2xl);
    padding: var(--spacing-xl);
    margin-bottom: var(--spacing-xl);
}

.summary-item {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.summary-item .label {
    font-size: 0.7rem;
    color: var(--secondary);
    text-transform: uppercase;
    letter-spacing: 0.15em;
    font-weight: 800;
}

.summary-item .value {
    font-size: 1.75rem;
    font-weight: 800;
    font-family: var(--font-mono);
}

.summary-item .value.positive {
    color: var(--fintech-green);
}

.value-link {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--accent);
    text-decoration: none;
}

.investment-grid {
    display: grid;
    grid-template-columns: 1fr 360px;
    gap: var(--spacing-xl);
}

.chart-main {
    height: 650px;
}

.tool-card.highlight {
    border-color: var(--accent);
    background: rgba(41, 151, 255, 0.05);
}

.btn-tiny {
    padding: 4px 12px;
    font-size: 0.75rem;
    font-weight: 800;
    background: var(--accent);
    color: #fff;
    border-radius: 4px;
    text-transform: uppercase;
}

/* Terminal View / MT5 Bridge */
.mt5-login-bridge {
    max-width: 450px;
    margin: 60px auto;
    padding: var(--spacing-2xl);
    text-align: center;
}

.lock-icon-container {
    margin-bottom: var(--spacing-lg);
}

.input-group {
    text-align: left;
    margin-bottom: var(--spacing-md);
}

.input-group label {
    display: block;
    font-size: 0.65rem;
    font-weight: 800;
    color: var(--secondary);
    margin-bottom: 6px;
    letter-spacing: 0.1em;
}

.input-group input,
.input-group select {
    width: 100%;
    padding: 12px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-md);
    color: #fff;
}

/* Connected Dashboard */
.connected-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--spacing-lg) var(--spacing-xl);
}

.broker-logo {
    font-weight: 900;
    letter-spacing: 0.2em;
    font-size: 1.2rem;
    color: #fff;
}

.balance-info {
    display: flex;
    gap: var(--spacing-2xl);
}

.balance-info .stat {
    display: flex;
    flex-direction: column;
}

.balance-info .label {
    font-size: 0.65rem;
    color: var(--secondary);
    text-transform: uppercase;
}

.balance-info .value {
    font-size: 1.25rem;
    font-weight: 800;
    font-family: var(--font-mono);
}

.terminal-table {
    width: 100%;
    border-collapse: collapse;
}

.terminal-table th {
    text-align: left;
    padding: 12px;
    font-size: 0.7rem;
    color: var(--secondary);
    border-bottom: 1px solid var(--glass-border);
}

.terminal-table td {
    padding: 16px 12px;
    font-size: 0.9rem;
}

/* Journal Command */
.journal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.journal-layout {
    display: grid;
    grid-template-columns: 1fr 400px;
    gap: var(--spacing-2xl);
    align-items: start;
}

.calendar-glass-container {
    padding: var(--spacing-xl);
}

.calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--spacing-lg);
    margin-bottom: var(--spacing-lg);
}

.calendar-grid-labels {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    text-align: center;
    font-size: 0.7rem;
    font-weight: 800;
    color: var(--secondary);
    padding-bottom: 20px;
}

.calendar-days {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 1px;
    background: var(--glass-border);
    border: 1px solid var(--glass-border);
}

.calendar-day {
    aspect-ratio: 1.1;
    background: rgba(255, 255, 255, 0.02);
    padding: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.3s ease;
}

.calendar-day.has-data {
    background: rgba(255, 255, 255, 0.04);
}

.calendar-day:hover {
    background: rgba(41, 151, 255, 0.05);
}

.day-num {
    font-size: 0.9rem;
    font-weight: 700;
    color: var(--secondary);
}

.day-stat {
    font-size: 0.75rem;
    font-weight: 800;
    text-align: center;
    padding: 4px;
    border-radius: 4px;
}

.day-stat.win {
    color: var(--fintech-green);
    background: rgba(48, 209, 88, 0.1);
}

.day-stat.loss {
    color: var(--fintech-red);
    background: rgba(255, 59, 48, 0.1);
}

/* Stats Section */
.stats-mesh {
    display: grid;
    grid-template-columns: 1fr;
    gap: var(--spacing-lg);
}

.perf-metric {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--spacing-md);
    background: rgba(255, 255, 255, 0.03);
    border-radius: var(--border-radius-md);
}

.perf-metric .label {
    font-size: 0.75rem;
    font-weight: 700;
    color: var(--secondary);
    text-transform: uppercase;
}

.perf-metric .value {
    font-size: 1.5rem;
    font-weight: 800;
}

.log-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.log-item {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 16px;
    background: rgba(255, 255, 255, 0.02);
    border-radius: var(--border-radius-md);
    border: 1px solid var(--glass-border);
}

.entry-type {
    font-size: 0.65rem;
    font-weight: 900;
    padding: 4px 8px;
    border-radius: 4px;
}

.entry-type.win {
    background: var(--fintech-green);
    color: #000;
}

.entry-type.loss {
    background: var(--fintech-red);
    color: #fff;
}

.entry-info {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.entry-info .symbol {
    font-weight: 700;
    font-size: 0.95rem;
}

.entry-info .date {
    font-size: 0.75rem;
    color: var(--secondary);
}

.entry-result {
    font-weight: 800;
    font-family: var(--font-mono);
}

.entry-result.positive {
    color: var(--fintech-green);
}

.entry-result.negative {
    color: var(--fintech-red);
}

/* Modal Styling */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(10px);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
    padding: 20px;
}

.modal-content {
    width: 100%;
    max-width: 440px;
    background: #0a0a0a;
    border: 1px solid var(--accent);
    padding: var(--spacing-2xl);
    position: relative;
    box-shadow: 0 0 50px rgba(41, 151, 255, 0.2);
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: var(--spacing-xl);
}

.modal-header h3 {
    font-size: 1.5rem;
    font-weight: 800;
}

.btn-close {
    background: transparent;
    border: none;
    color: var(--secondary);
    font-size: 2rem;
    cursor: pointer;
    line-height: 1;
}

.type-toggle {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
}

.toggle-btn {
    padding: 12px;
    border-radius: var(--border-radius-md);
    border: 1px solid var(--glass-border);
    background: rgba(255, 255, 255, 0.05);
    color: var(--secondary);
    font-weight: 800;
    font-size: 0.8rem;
    cursor: pointer;
    transition: all 0.3s ease;
}

.toggle-btn.win.active {
    background: var(--fintech-green);
    color: #000;
    border-color: var(--fintech-green);
}

.toggle-btn.loss.active {
    background: var(--fintech-red);
    color: #fff;
    border-color: var(--fintech-red);
}

/* Indicator Tab Styling */
.indicator-view {
    max-width: 1200px;
    margin: 0 auto;
    padding-bottom: 80px;
}

.indicator-hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--spacing-3xl) var(--spacing-2xl);
    background: linear-gradient(135deg, rgba(41, 151, 255, 0.12) 0%, rgba(10, 10, 10, 0.95) 100%);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-xl);
    position: relative;
    overflow: hidden;
    margin-bottom: 40px;
}

.indicator-hero::before {
    content: "";
    position: absolute;
    top: -50%;
    left: -20%;
    width: 100%;
    height: 200%;
    background: radial-gradient(circle, rgba(41, 151, 255, 0.05) 0%, transparent 70%);
    pointer-events: none;
}

.badge-premium {
    font-size: 0.7rem;
    font-weight: 900;
    color: var(--accent);
    letter-spacing: 0.4em;
    margin-bottom: var(--spacing-md);
    text-transform: uppercase;
    display: flex;
    align-items: center;
    gap: 12px;
}

.badge-premium::after {
    content: "";
    width: 40px;
    height: 1px;
    background: var(--accent);
    opacity: 0.5;
}

.indicator-hero h1 {
    font-size: 4rem;
    font-weight: 900;
    line-height: 1;
    margin-bottom: var(--spacing-lg);
    letter-spacing: -0.02em;
    background: linear-gradient(180deg, #fff 0%, rgba(255, 255, 255, 0.7) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.indicator-hero .description {
    font-size: 1.25rem;
    color: var(--secondary);
    max-width: 540px;
    margin-bottom: var(--spacing-2xl);
    line-height: 1.5;
}

.glow-icon {
    color: var(--accent);
    filter: drop-shadow(0 0 50px rgba(41, 151, 255, 0.4));
    opacity: 1;
    animation: pulse-glow 4s infinite alternate ease-in-out;
}

@keyframes pulse-glow {
    from {
        filter: drop-shadow(0 0 30px rgba(41, 151, 255, 0.2));
        transform: scale(0.95);
    }

    to {
        filter: drop-shadow(0 0 60px rgba(41, 151, 255, 0.5));
        transform: scale(1.05);
    }
}

.section-title {
    font-size: 0.8rem;
    font-weight: 800;
    color: var(--accent);
    margin-top: 50px;
    margin-bottom: 24px;
    text-transform: uppercase;
    letter-spacing: 0.25em;
    display: flex;
    align-items: center;
    gap: 16px;
}

.section-title::after {
    content: "";
    flex: 1;
    height: 1px;
    background: linear-gradient(90deg, var(--glass-border) 0%, transparent 100%);
}

.components-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 24px;
}

.comp-card {
    position: relative;
    padding: var(--spacing-xl);
    background: rgba(255, 255, 255, 0.02);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-lg);
    transition: all 0.4s cubic-bezier(0.16, 1, 0.3, 1);
    overflow: hidden;
}

.comp-card::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, var(--accent), transparent);
    transform: translateX(-100%);
    transition: transform 0.6s ease;
}

.comp-card:hover {
    background: rgba(255, 255, 255, 0.04);
    border-color: rgba(41, 151, 255, 0.4);
    transform: translateY(-6px);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.comp-card:hover::before {
    transform: translateX(0);
}

.comp-icon {
    width: 52px;
    height: 52px;
    background: rgba(41, 151, 255, 0.1);
    border-radius: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--accent);
    margin-bottom: 24px;
    transition: all 0.4s ease;
}

.comp-card:hover .comp-icon {
    background: var(--accent);
    color: #000;
    transform: scale(1.1);
}

.comp-card h4 {
    font-size: 1.1rem;
    font-weight: 800;
    margin-bottom: 12px;
    color: #fff;
    letter-spacing: -0.01em;
}

.comp-card p {
    font-size: 0.85rem;
    color: var(--secondary);
    line-height: 1.6;
}

.indicator-split {
    display: grid;
    grid-template-columns: 1.4fr 1fr;
    gap: 40px;
    align-items: stretch;
    margin-top: 40px;
}

.strategy-flow {
    padding: var(--spacing-3xl);
    background: rgba(255, 255, 255, 0.01);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-xl);
}

.strategy-flow h3 {
    font-size: 1.5rem;
    font-weight: 900;
    margin-bottom: var(--spacing-lg);
}

.flow-steps {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    margin-top: var(--spacing-2xl);
}

.flow-group .label {
    display: inline-block;
    padding: 8px 18px;
    border-radius: 6px;
    font-size: 0.75rem;
    font-weight: 900;
    margin-bottom: 20px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}

.flow-group .label.long {
    background: rgba(48, 209, 88, 0.12);
    color: var(--fintech-green);
    border: 1px solid rgba(48, 209, 88, 0.2);
}

.flow-group .label.short {
    background: rgba(255, 59, 48, 0.12);
    color: var(--fintech-red);
    border: 1px solid rgba(255, 59, 48, 0.2);
}

.flow-group ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.flow-group li {
    font-size: 0.85rem;
    color: var(--secondary);
    margin-bottom: 12px;
    padding-left: 22px;
    position: relative;
    line-height: 1.4;
}

.flow-group li::before {
    content: "→";
    position: absolute;
    left: 0;
    color: var(--accent);
    font-weight: 900;
}

.pro-tips-area {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.pro-tips-area .glass-panel {
    background: rgba(255, 255, 255, 0.01);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-xl);
    padding: var(--spacing-2xl);
    transition: all 0.3s ease;
}

.pro-tips-area .glass-panel:hover {
    background: rgba(41, 151, 255, 0.02);
    border-color: rgba(41, 151, 255, 0.2);
}

.highlight-accent {
    border-left: 4px solid var(--accent) !important;
    background: linear-gradient(90deg, rgba(41, 151, 255, 0.05) 0%, transparent 100%) !important;
}

.pro-tips-area h3 {
    font-size: 1.25rem;
    font-weight: 800;
    margin-bottom: 12px;
}

.tips-list {
    list-style: none;
    padding: 0;
    margin-top: 24px;
}

.tips-list li {
    font-size: 0.9rem;
    color: var(--secondary);
    margin-bottom: 16px;
    display: flex;
    gap: 16px;
    line-height: 1.5;
    padding: 12px;
    background: rgba(255, 255, 255, 0.02);
    border-radius: 8px;
    border: 1px solid transparent;
    transition: all 0.3s ease;
}

.tips-list li:hover {
    border-color: var(--glass-border);
    background: rgba(255, 255, 255, 0.04);
}

.tips-list li::before {
    content: "✓";
    color: var(--fintech-green);
    font-weight: 900;
    font-size: 1.1rem;
}

@media (max-width: 1024px) {
    .indicator-hero {
        flex-direction: column;
        text-align: center;
    }

    .indicator-hero .description {
        margin: 0 auto var(--spacing-xl);
    }

    .components-grid {
        grid-template-columns: repeat(2, 1fr);
    }

    .indicator-split {
        grid-template-columns: 1fr;
    }

    .flow-steps {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 600px) {
    .components-grid {
        grid-template-columns: 1fr;
    }
}

/* Onboarding Cards */
.onboarding-steps-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--spacing-lg);
}

.setup-step-card {
    padding: var(--spacing-xl);
    position: relative;
    overflow: hidden;
}

.step-num {
    position: absolute;
    top: -10px;
    right: -10px;
    font-size: 4rem;
    font-weight: 900;
    opacity: 0.05;
}

/* Calculator */
.calculator-container {
    max-width: 900px;
    margin: 40px auto;
    padding: var(--spacing-2xl);
}

.calc-grid {
    display: grid;
    grid-template-columns: 1fr 1.2fr;
    gap: var(--spacing-2xl);
    margin-top: var(--spacing-xl);
    align-items: start;
}

.glass-input,
.glass-select {
    width: 100%;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-md);
    padding: 14px;
    color: #fff;
    font-size: 1rem;
    margin-top: 8px;
    transition: border-color 0.3s ease;
}

.glass-input:focus {
    border-color: var(--accent);
    outline: none;
}

.calc-results-mesh {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
}

.result-card {
    padding: var(--spacing-xl);
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.result-card.accent {
    background: linear-gradient(135deg, rgba(41, 151, 255, 0.15) 0%, transparent 100%);
    border: 1px solid var(--accent);
    box-shadow: 0 0 30px rgba(41, 151, 255, 0.1);
}

.result-card .label {
    font-size: 0.65rem;
    font-weight: 800;
    color: var(--secondary);
    text-transform: uppercase;
    letter-spacing: 0.1em;
}

.result-card.accent .value {
    font-size: 3.5rem;
    font-weight: 900;
    line-height: 1;
    color: #fff;
    font-family: var(--font-mono);
}

.result-card .unit {
    font-size: 1rem;
    font-weight: 800;
    color: var(--accent);
}

.sub-results-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: var(--spacing-md);
}

.result-card.mini {
    padding: var(--spacing-md);
}

.result-card.mini .value-sm {
    font-size: 1.25rem;
    font-weight: 800;
    font-family: var(--font-mono);
}

.flex-baseline {
    display: flex;
    align-items: baseline;
}

@media (max-width: 1024px) {

    .investment-grid,
    .journal-grid,
    .calc-grid,
    .onboarding-steps-grid {
        grid-template-columns: 1fr;
    }
}